<template>
  <div>
    <header class="mui-bar mui-bar-nav">
      <button class="mui-btn mui-btn-blue mui-btn-link mui-btn-nav mui-pull-left" @click="goBack">
        <span class="mui-icon mui-icon-left-nav"></span>
      </button>
      <h1 class="mui-title"></h1>
      <!-- <div class="headerNews">
        <a class="mui-icon mui-icon-bars mui-pull-right"></a>
        <span></span>
      </div> -->
    </header>
    <div class="mui-content">
      <div class="policyTab">
        <ul>
          <li>
            <a :class="isCar?'xz':''" @click="changeClass('car','006')" href="javascript:return false;"></a>
          </li>
          <li>
            <a :class="nonCar?'xz':''" @click="changeClass('nonCar','004')" href="javascript:return false;"></a>
          </li>
        </ul>
      </div>
      <div v-if="list==null||list==''" class="policyList wb100 noData">
        <img :src="icon_shape" alt="">
        <span></span>
      </div>
      <div v-else class="policyList wb100" v-show="true">
        <ul class="wb100">
          <li v-for="(item,index) in list">
            <div class="liHeader" @click="loadAnim(index)">
              <img class="bd" :src="icon_bd" alt="" />
              <em>NO.{{index+1}}</em>
              <span>
                <img :class="((index==0?true:false))?'arrow arrowBottom':'arrow'" :src="icon_yj" alt="" />
              </span>
            </div>
            <div class="wb100 listBox" v-show="index==0?true:false">
              <ul class="listText">
                <li>
                  <span></span>
                  <span class="colBlue">{{item.cPlyNo}}</span>
                </li>
                <li>
                  <span></span>
                  <span>{{item.cProdName}}</span>
                </li>
                <li>
                  <span></span>
                  <span>{{item.tAppTm}}</span>
                </li>
                <li>
                  <span></span>
                  <span>{{item.cKindNo}}</span>
                </li>
                <li>
                  <span></span>
                  <span>{{item.cPlyStatName}}</span>
                </li>
                <li>
                  <span></span>
                  <span v-if="item.nPrm!=null&&item.nPrm!=''">{{item.nPrm}}</span>
                  <span v-else-if="item.nPlyPrmAmt!=null&&item.nPlyPrmAmt!=''">{{item.nPlyPrmAmt}}</span>
                  <span v-else>0.0</span>
                </li>
                <li>
                  <span class="yellow"></span>
                  <span v-if="item.reNewinsu=='1'"></span>
                  <span v-else-if="item.reNewinsu=='0'"></span>
                  <span v-else></span>
                </li>
              </ul>
            </div>
            <div class="listBtn wb100">
              <a href="javascript:return false;" @click="showEdrList"></a>
              <a href="javascript:return false;" @click="showClaimList(item.cPlyNo)"></a>
              <a class="xz" href="javascript:return false;" @click="goPlyInfo(item.cPlyNo)"></a>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!---->
    <div class="divHideNew" style="display: none">
      <div class="divTriangle"></div>
      <div class="triangleBox">
        <ul>
          <li>
            <a href="index1.html">
              <i class="iconfont icon-shouye"></i>
            </a>
          </li>
          <li>
            <a href="#">
              <i class="iconfont icon-xiaoxi"></i>
            </a>
            <span></span>
          </li>
          <li>
            <a href="#">
              <i class="iconfont icon-bangzhu"></i>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import bd from './source/icon_bd.png';
import yj from './source/icon_yj.png';
import shape from './source/Shape@3x.png';
import axios from "axios";
import $ from "jquery";
export default {
  data() {
    return {
      giveChild: "",
      icon_bd: bd,
      icon_yj: yj,
      icon_shape: shape,
      list: [],
      isCar:true,
      nonCar:false,
      queryListType: '006',
      userCode:this.$route.query.userCode
    };
  },
  mounted(){
      alert(this.userCode);
      this.changeClass('car','006');
  },
  methods:{
      loadAnim:function (index) {
        var temp = this.list;
        temp[index].isShow = !temp[index].isShow;
        this.list = temp;
    },
    changeClass:function(selType,queryType){
        if(selType=='car'){
            this.isCar = true;
            this.nonCar = false;
            this.queryListType = '006';
        }else if(selType == 'nonCar'){
          this.isCar = false;
          this.nonCar = true;
          this.queryListType = '004';
        }
        axios({
          method: "post",
          url: "NonVehiclePolicyList/infoList",
          params: {
            name: "",
            type: queryType,
            userCode: this.userCode
          }
        })
        .then(response => {
            this.list = response.data;
          })
          .catch(error => {
            alert(error);
          });
    },
    goPlyInfo: function(plyNo) {
        this.$router.push({
            name: 'policyInfo',
            query: {
                policyNo: plyNo ,
                type: this.queryListType               
            }
        })
    },
    showEdrList: function() {

    },
    showClaimList: function(plyNo) {
        this.$router.push({
          name: 'ClaimListInfo',
          params: {
              policyNo: plyNo,
              queryListType: this.queryListType
          }
        })
    },
    goBack: function() {
      window.history.go(-1);
    }
  }
//   },
//   components: {
//     Header
//   }
};
</script>
<style scoped>
@import '../../assets/MUI/css/style.css';
</style>




